/*
 * Injected script is used to gain access to JS context
 * Notifies every time when AJAX call is completed which potentially adds new content to the page.
 */
import { INTERNAL_OUT } from '../const'
import { dispatch } from 'utils'
import $ from 'jquery'

window.onbeforeunload = function () {
  if ($('body').hasClass('blockunload')) return 'Action in progress'
}

setTimeout(function () {
  $(document).ajaxComplete(function () {
    dispatch(INTERNAL_OUT.AJAX_FINISHED)
  })
}, 0)

$('[data-order-id]').attr('onclick', 'showPopup(this)') // inject the code that triggers the popup for Purchase Now button
